import * as React from 'react';
import type { Controller, Inline, ZhiPlugin } from "@ali/4ever-cangjie";
import Mention from "../mention";

export function createRenderNode(mentionConfig: Record<string, any>): ZhiPlugin["renderNode"] {
  const { onMentionClick, onMentionHover, getCurrentUser, locale = {} } = mentionConfig;

  const renderNode: ZhiPlugin["renderNode"] = (props, _: Controller) => {
    const { node } = props;
    const { attributes } = props;
    return (
      <Mention
        {...attributes}
        node={node as Inline}
        onMentionClick={onMentionClick}
        onMentionHover={onMentionHover}
        getCurrentUser={getCurrentUser}
        locale={locale}
      />
    );
  }
  return {
    mention: renderNode,
  };
}
